<template>
  <div class="testProject">
    <div class="function">
      <el-row>
        <el-col :span="4">
          <div class="grid-content bg-purple">
            <el-input
              placeholder="ID"
              icon="search"
              v-model="input">
            </el-input>
          </div>
        </el-col>
        <el-col :span="20">
          <el-button type="primary"></el-button>
        </el-col>
      </el-row>
    </div>
    <div id="table">
      <el-table
        :data="tableData"
        stripe
        style="width: 100%">
        <el-table-column
          prop="id"
          label="ID">
        </el-table-column>
        <el-table-column
          prop="testName"
          label="">
        </el-table-column>
        <el-table-column
          prop="types"
          label="">
        </el-table-column>
        <el-table-column
          prop="testUser"
          label=""
          width=140>
        </el-table-column>
        <el-table-column
          prop="projectID"
          label="ID"
          width=180>
        </el-table-column>
        <el-table-column
          prop="create"
          label="">
        </el-table-column>
        <el-table-column
          prop="lastUpdate"
          label=""
          width=150>
        </el-table-column>
        <el-table-column
          prop="status"
          label="">
        </el-table-column>

        <el-table-column
          :context="_self"
          inline-template
          label=""
          width=120>
          <span>
            <el-button @click="handleClick" type="text" size="small"></el-button>
            <el-button type="text" size="small"></el-button>
          </span>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
  import tableData from '../../mock/testProject.js';
  export default {
    data(){
      return {
        input: '',
        tableData
      }

    },
    methods: {
      handleClick: function(){
        alert('click');
      }
    }
  }
</script>

<style media="screen">
  .function{
    margin-bottom:10px;
  }
  .function button{
    float:right;
  }
</style>



